import React from 'react';
import PropTypes from 'prop-types';
import SmoothScrollbar from 'smooth-scrollbar';
import './Scrollbar.css'

export default class Scrollbar extends React.Component {

  constructor(props) {
    super(props);

    this.callbacks = [];
  }

  // use context in child component
  getChildContext() {
    // console.log('get', this.callbacks)
    return {
      getScrollbar: (cb) => {
        if (typeof cb !== 'function') return;

        if (this.scrollbar) setTimeout(() => cb(this.scrollbar));
        else this.callbacks.push(cb);
      }
    };
  }

  componentDidMount() {
    // console.log('did mount')
    // get parent component ref
    this.scrollbar = new SmoothScrollbar(this.$container, this.props);

    this.callbacks.forEach((cb) => {
      requestAnimationFrame(() => cb(this.scrollbar));
    });

    this.scrollbar.addListener(this.handleScroll.bind(this));

    // console.log(this.scrollbar.__proto__)

    // move scroll
    // this.scrollbar.scrollTo(1000,0,2500, (data)=>{
    //   // console.log('scroll to', data)
    // })
  }

  componentWillUnmount() {
    console.log('will unmount')
    this.scrollbar.destroy(true);
  }

  componentWillReceiveProps(nextProps) {
    // console.log('will receive')
    this.scrollbar.setOptions(nextProps);
  }

  componentDidUpdate() {
    // console.log('did update', this.scrollbar && this.scrollbar.update())
    this.scrollbar && this.scrollbar.update();
  }

  handleScroll(status) {
    // console.log('handle scroll')
    if (this.props.onScroll) {
      console.log(this.props.onScroll)
      // console.log('on', status.offset.x)
      this.props.onScroll(status, this.scrollbar);
    }
  }

  render() {
    const {
      speed,
      damping,
      thumbMinSize,
      syncCallbacks,
      renderByPixels,
      alwaysShowTracks,
      continuousScrolling,
      overscrollEffect,
      overscrollEffectColor,
      overscrollDamping,
      onScroll,

      children,
      ...others,
    } = this.props;

    return (
      <section data-scrollbar ref={element => this.$container = element} {...others}>
          <article className="scroll-content">
              {children}
          </article>
          <aside className="scrollbar-track scrollbar-track-x">
              <div className="scrollbar-thumb scrollbar-thumb-x"></div>
          </aside>
          <aside className="scrollbar-track scrollbar-track-y">
              <div className="scrollbar-thumb scrollbar-thumb-y"></div>
          </aside>
          <canvas className="overscroll-glow"></canvas>
      </section>
    );
  }
}


Scrollbar.propTypes = {
  speed: PropTypes.number,
  damping: PropTypes.number,
  thumbMinSize: PropTypes.number,
  syncCallbacks: PropTypes.bool,
  renderByPixels: PropTypes.bool,
  alwaysShowTracks: PropTypes.bool,
  continuousScrolling: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
  ]),
  overscrollEffect: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
  ]),
  overscrollEffectColor: PropTypes.string,
  overscrollDamping: PropTypes.number,
  onScroll: PropTypes.func,
  children: PropTypes.node,
};

Scrollbar.childContextTypes = {
  getScrollbar: PropTypes.func,
};



