import React, { Component } from 'react';
// import '../../node_modules/smooth-scrollbar/dist/smooth-scrollbar.css';
import Scrollbar from '../components/scrollbar/Scrollbar';

class ImageSlider extends Component {

  render() {
    // console.log('img', this.props.isImgSlider)
    return (
      <Scrollbar speed={0.5} overscrollEffect='bounce' >
        <img src={require('../img/1.jpg')} alt='' onClick={()=>console.log('deer')}/>
        <img src={require('../img/2.jpg')} alt=''/>
        <img src={require('../img/3.jpg')} alt=''/>
        <img src={require('../img/4.jpg')} alt=''/>
        <img src={require('../img/5.jpg')} alt=''/>
      </Scrollbar>
    );
  }

}

export default ImageSlider;



