import React, {Component} from 'react';
import './style.css'
import Loading from './containers/loading/Loading'
import ImageSlider from './containers/ImageSlider'
import styled from 'styled-components';

import ImagesLoaded from './components/utills/ImagesLoaded'

const Container = styled.h1`
  width: 100%;
  height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isImgSlider: false,
      images: [
        { src: 'https://media.licdn.com/mpr/mpr/shrinknp_800_800/AAEAAQAAAAAAAAfUAAAAJGI4Mjg4ZDcwLWE1MDUtNDU2My04ODI5LTIzYTM1NzUxZDM5Yg.jpg' },
      ]
    };
    this._getImgSlider = this._getImgSlider.bind(this);
  }

  _getImgSlider(visible) {
    this.setState({
      isImgSlider: visible,
    },() => {
      console.log('parent',visible);
    });
  }

  handleOnAlways = (instance) => {
    console.log('ALWAYS - all images have been loaded');
  }

  handleDone = () => {
    console.log('DONE  - all images have been successfully loaded');
  }

  handleOnFail = () => {
    console.log('FAIL - all images loaded, at least one is broken');
  }

  handleOnProgress = (instance, image) => {
    const result = image.isLoaded ? 'loaded' : 'broken';
    console.log( 'image is ' + result + ' for ' + image.img.src );
  }

  renderImages() {
    const { images } = this.state;
    return images.map((item, index) => {
      return (
        <img key={index} src={item.src} />
      )
    })
  }

  addImage = () => {
    this.setState({
      images: [
        ...this.state.images,
        {
          src: "https://media.licdn.com/mpr/mpr/shrinknp_800_800/AAEAAQAAAAAAAAfUAAAAJGI4Mjg4ZDcwLWE1MDUtNDU2My04ODI5LTIzYTM1NzUxZDM5Yg.jpg"
        }
      ]
    })
  }

  reset = () => {
    this.empty(this.refs.imgLoad)
  }

  empty( elem ) {
    // console.log(elem)
    while (elem.refs.elemContainer.firstChild) {
    //   console.log('while')
      elem.refs.elemContainer.removeChild(elem.refs.elemContainer.firstChild)
    }
  }

  getItemsFragment() {
    var fragment = document.createDocumentFragment
  }

  render() {
    // console.log('render')

    const btnStyle = {
      border: 0,
      background: 'salmon',
      color: 'black',
      padding: '10px 30px',
    };

    const containerStyles = {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      flexDirection: 'column'
    }

    return (
      // <Container>
      //   <Loading getImgSlider={this._getImgSlider} />
      //   <div style={{position:'absolute', display: this.state.isImgSlider ? 'block':'none'}}>
      //     <ImageSlider isImgSlider={this.state.isImgSlider} />
      //   </div>
      // </Container>
      <div style={containerStyles}>
        <button onClick={this.addImage} style={btnStyle}>Add image</button>
        <button onClick={this.reset} style={btnStyle}>Reset</button>

        <ImagesLoaded
          ref='imgLoad'
          elementType={'div'}
          onAlways={this.handleOnAlways}
          done={this.handleDone}
          onFail={this.handleOnFail}
          onProgress={this.handleOnProgress}
          className={'My-new-class'}
        >
          {this.renderImages()}
        </ImagesLoaded>
      </div>
    );
  }
}


export default App;



